<template>
    <div class="views collection">
        <navigate title="我的收藏" position="absolute" @back="handlerBack" back/>
        <div class="collection-container scroll-wrapper" v-if="list.length">
            <mu-list style="padding: 0;">
                <mu-list-item class="border-1px-b" v-for="(item, index) in list" :key="index"
                              @click="handlerItem(item)"
                              avatar button>
                    <mu-list-item-content>
                        <mu-list-item-title>{{item.name || item.title}}</mu-list-item-title>
                        <mu-list-item-sub-title>距离：{{item.distance}} 公里
                        </mu-list-item-sub-title>
                    </mu-list-item-content>
                    <mu-list-item-action>{{item.time | formatDate('YYYY-MM-DD')}}</mu-list-item-action>
                </mu-list-item>
            </mu-list>
        </div>
        <no-success :text="`暂时没有搜索到 我的收藏 列表`" v-else/>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import FormatTime from '../../../common/utils/time'
    import NoSuccess from "../../../components/no-success/no-success"
    import {queryIAddress, queryILabel} from "../../../api/personal"

    export default {
        name: "collection",
        data() {
            return {
                params: {
                    pageNumber: 0,
                    pageSize: 20
                },
                list: []
            }
        },
        props: {
            value: {}
        },
        activated() {
            this.queryIAddress()
        },
        methods: {
            // 点击返回
            handlerBack() {
                this.$emit('input', false)
            },
            // 点击列表
            handlerItem(item) {
                item.name = item.name || item.title
                item.x = item.x || item.longitude
                item.y = item.y || item.latitude

                this.$emit('change', item)
            },
            queryIAddress() {
                this.params.pageNumber = 0
                queryIAddress(this.params).then(res => {
                    this.list = res.content
                })
            },
            queryILabel() {
                this.params.pageNumber = 0
                queryILabel(this.params).then(res => {
                    this.list = res.content
                })
            }
        },
        filters: {
            formatDate: FormatTime.formatDate
        },
        components: {
            NoSuccess,
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .collection
        padding-top 46px
        background-color #fff
        .collection-container
            width 100%
            height 100%

</style>



